<div class="upload"><div class="subtitle">Upload dataset file:</div><div class="file-picker"><div class="input"><label for="upload">Dataset filename:</label><input id="template" type="file" accept=".pbtxt,.pb,.xlsx,.csv" v-on:change="(e) =&gt; setFile(e)"></div></div><div class="submit"><button id="upload-submit" @click="submitUpload">Submit Upload</button></div><div class="copy">Choose a &nbsp;<code>.pbtxt/.xlsx/.csv</code>&nbsp; file compiled with the &nbsp;<code>construct_dataset.py</code>&nbsp; script in the &nbsp;<a href="https://github.com/Center-for-Mechanical-Control-of-Chem/cmccdb-schema">cmccdb-schema repository</a></div></div>